import { useState, useEffect } from 'react';
import { Box, Card, CardHeader, CardMedia, CardContent, Typography, IconButton, Avatar } from '@mui/material';
import { Delete, ChatBubbleOutline } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useAuth } from '../contexts/AuthContext';
import config from '../config';

function Dashboard() {
  const [posts, setPosts] = useState([]);
  const navigate = useNavigate();
  const { user } = useAuth();

  useEffect(() => {
    fetchPosts();
  }, []);

  const fetchPosts = async () => {
    try {
      const response = await axios.get(`${config.API_URL}/api/posts`);
      setPosts(response.data);
    } catch (error) {
      console.error('Error fetching posts:', error);
    }
  };

  const handleDelete = async (postId) => {
    if (!window.confirm('Are you sure you want to delete this post?')) return;
    
    try {
      await axios.delete(`${config.API_URL}/api/posts/${postId}`);
      setPosts(posts.filter(post => post._id !== postId));
    } catch (error) {
      console.error('Error deleting post:', error);
      alert('Failed to delete post');
    }
  };

  return (
    <Box sx={{ maxWidth: 600, mx: 'auto' }}>
      {posts.map(post => (
        <Card key={post._id} sx={{ mb: 2 }}>
          <CardHeader
            avatar={
              <Avatar src={`${config.API_URL}${post.user.profilePhoto}`} />
            }
            action={
              post.user._id === user._id && (
                <IconButton onClick={() => handleDelete(post._id)}>
                  <Delete />
                </IconButton>
              )
            }
            title={post.user.displayName}
            subheader={new Date(post.createdAt).toLocaleDateString()}
          />
          <CardMedia
            component="img"
            image={`${config.API_URL}${post.image}`}
            alt={post.caption}
            sx={{ cursor: 'pointer' }}
            onClick={() => navigate(`/posts/${post._id}`)}
          />
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 1 }}>
              <Typography 
                variant="body1"
                sx={{ 
                  fontSize: '1rem',
                  fontWeight: 500
                }}
              >
                {post.caption}
              </Typography>
            </Box>
            <Box sx={{ mt: 1, display: 'flex', alignItems: 'center' }}>
              <ChatBubbleOutline sx={{ mr: 1, fontSize: 20 }} />
              <Typography variant="body2" color="text.secondary">
                {post.commentCount} comments
              </Typography>
            </Box>
          </CardContent>
        </Card>
      ))}
    </Box>
  );
}

export default Dashboard; 